#mt-sidenav {
    background-color: transparent;
    height: 100%;

    #sidebar-lock-button {
        .absolute();
        z-index: 25;
        font-size: 22px;
        .top-right(30px, 6px);
    }

    #mt-menu-group-wrapper {
        width: 64px;
        padding: 0;
        height: 100%;
        background-color: @base_dark;

        #nav-user-profile {
            .nopadding();
            .nomargin();
            background-color: rgba(255, 255, 255, .1);

            button {
                .nopadding();
                width: 100%;
                height: 100%;
                .nomargin();

                img {
                    .square(48px);
                    .rounded();
                    .absolute();
                    .spread();
                    margin: auto;
                }
            }

            &:after {
                display: none;
            }
        }

        mt-menu-item {
            .cursor();
            .block();
            width: 64px;
            height: 64px;
            text-align: center;
            line-height: 64px;
            .relative();
            overflow: hidden;
            box-sizing: border-box;
            .transition(background-color .2s ease);

            &:last-child {
                border-bottom: 0;
            }

            mt-notif-counter {
                .absolute();
                .top-right(10px, 10px);
                height: 14px;
                width: 14px;
                line-height: 14px;
                background: linear-gradient(45deg, #ff598f 0%, #ff6f5a 100%);
                border-radius: 50%;
                font-size: 10px;
                font-weight: 800;
                color: white;
            }

            &:after {
                content: 'after';
                text-indent: -99999px;
                .absolute();
                left: -4px;
                top: 0;
                width: 4px;
                height: 100%;
                .gradient-soft();
                .transition(transform .2s ease);
            }

            md-icon {
                margin: auto;
            }

            i.menu-icon {
                color: #fff;
                font-size: 22px;
            }

            &:hover {
                background-color: rgba(255, 255, 255, .05);
            }

            &.active {
                background-color: rgba(0, 0, 0, .3);
                border: 0;

                &:after {
                    transform: translate3d(4px, 0, 0);
                }
            }

        }
    }

    #mt-submenu-wrapper {
        .relative();
        position: 20;
        width: 240px;
        background-color: transparent;

        md-list {
            width: 100%;
            .absolute();

            h3 {
                letter-spacing: 1px;
            }

            h3, md-list-item {
                .transition();
                opacity: 1;

                .translate3d(0, 0, 0);

                p {
                    font-size: 15px;
                    text-transform: capitalize;
                    font-weight: 300;
                }

                &:nth-child(2) {
                    .transition-delay(.1s);
                }

                &:nth-child(3) {
                    .transition-delay(.13s);
                }

                &:nth-child(4) {
                    .transition-delay(.18s);
                }

                &:nth-child(5) {
                    .transition-delay(.28s);
                }

                &:nth-child(6) {
                    .transition-delay(.4s);
                }

                &:nth-child(7) {
                    .transition-delay(.5s);
                }

                &:nth-child(8) {
                    .transition-delay(.7s);
                }

                &:nth-child(9) {
                    .transition-delay(1s);
                }

                &:nth-child(10) {
                    .transition-delay(2s);
                }
            }

            md-list-item {

                &.current {

                    a {
                        background-color: rgba(158, 158, 158, 0.2);
                    }
                }

                mt-submenu-notif {
                    .absolute();
                    right: 16px;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    height: 16px;
                    width: 26px;
                    border: 1px solid #969696;
                    color: #969696;
                    font-size: 12px;
                    line-height: 16px;
                    text-align: center;
                    border-radius: 12px;
                    font-weight: 500;
                }

                .md-button:first-child {
                    padding: 0 20%;
                    margin: 0;
                }
            }

            &.ng-hide {
                display: block !important;
                pointer-events: none;

                h3, md-list-item {
                    opacity: 0;
                    .translate3d(-40px, 0, 0);
                    //.transition-duration(0ms);
                    .transition-delay(0s);

                }
            }
        }
    }
}

.md-open-menu-container {
    i.menu-icon {
        font-size: 18px;
        line-height: 1;
        margin: auto 16px auto 0;
        display: inline-block;
        color: #959799;
    }
}

