.mt-component {
    &.mt-table {

        *::-webkit-scrollbar {
            display: none;
        }

        .table-topbar {
            .absolute();
            .top-left(0);
            width: 100%;
            background-color: white;
            z-index: 10;

            h2 {
                text-indent: 26px;
                font-weight: 500;
                color: #3d3d3d;
                font-size: 22px;
            }

            > .md-button {
                margin: 10px 6px 0 12px;
            }

            .mt-module-list-action-wrapper {
                padding: 8px 14px;

                md-menu {
                    padding: 0;
                }
            }

            mt-module-list-header {
                padding: 0 16px;

                .mt-module-list-checkbox {
                    margin: 18px 12px;
                }

                div {
                    line-height: 56px;
                    color: #646464;
                    font-weight: 500;
                }

                .mt-module-list-header-sort {
                    .relative();
                    .cursor();

                    md-icon {
                        opacity: 0;
                        .absolute();
                        .push-right(12px);
                        transform: scale(.8);

                        &:first-child {
                            top: -8px;
                        }

                        &:last-child {
                            bottom: -8px;
                        }
                    }
                    &:hover {
                        md-icon {
                            opacity: .2;
                        }
                    }

                    &.active {

                        &.sortUp {

                            md-icon:first-child {
                                opacity: .6;
                            }
                        }

                        &.sortDown {

                            md-icon:last-child {
                                opacity: .6;
                            }
                        }
                    }
                }

            }
        }

        .mt-data-table-content {
            max-height: calc(~"100vh - 118px");
            margin-top: 118px;
            overflow-y: scroll;

            md-list {
                .fwidth();
                .noselect();
                padding-bottom: 0;

                md-list-item, mt-module-list-header {
                    padding: 4px 0;
                    border-bottom: 1px solid #f6f6f6;

                    .mt-module-list-checkbox {
                        margin: 0 20px 0 12px;
                    }

                    .mt-module-list-menu {
                        .absolute();
                        right: 14px;
                        padding: 4px 0;
                    }
                }

                &.striped {
                    md-list-item {
                        &:nth-child(even) {
                            //background-color: #fafafa;
                        }
                        .transition(background-color .1s ease);
                        border-bottom-color: #f1f3f8;
                        &:hover {
                            background-color: rgba(10, 20, 30, .05);
                        }
                    }

                }

                .mt-module-list-error {
                    .absolute();
                    .spread(0);
                    background: rgba(255, 255, 255, .75);
                    z-index: 15;

                    md-icon {
                        width: 48px;
                        height: 48px;

                        animation: spin 1s ease infinite;
                    }
                }
            }
        }

        .mt-module-list-footer {
            padding: 12px;
            color: #545454;
            font-size: 14px;
            font-weight: 500;

            .mt-module-list-limit {
                margin: -4px 20px 0 20px;

                &.md-input-focused {
                    margin-bottom: 1px;
                }

                md-select-value {
                    min-width: 32px;
                    border: 0;

                    .md-select-icon {
                        margin: 0;
                    }
                }
            }

            button {
                margin: 0;

                &:last-child {
                    margin-right: 10px;
                }
            }
        }
    }
}

.card-block {
    background-color: #fff;
    box-shadow: 0 0px 3px rgba(0, 0, 0, .05), 0 0px 30px -5px rgba(0, 0, 0, .1);
    .transition(all .4s ease);
    &:hover {
        box-shadow: 0 0px 6px rgba(0, 0, 0, .1), 0 10px 40px -5px rgba(0, 0, 0, .1);
    }
}

.content-module-block {
    margin: 8px 0;

    h3 {
        margin: 0;
        font-size: 11px;
        padding-bottom: 30px;
        text-transform: uppercase;
        font-weight: bold;
    }
}

.mt-page-module-wrapper {
    margin: 26px 0;
    overflow: visible;

    .mt-page-module-header {
        padding-top: 30px;
        .relative();
        z-index: 2;
        h3 {
            margin: 0;
            font-size: 11px;
            padding-bottom: 12px;
            text-transform: uppercase;
            font-weight: bold;
        }
        .mt-page-module-action > * {
            .transition(all .2s ease);
            .translate3d(5px, 0px, 0);
            opacity: 0;
            i.menu-icon {
                font-size: 28px;
                line-height: 1;
            }
        }

        .mt-page-module-action {
            margin-top: -25px;
            margin-right: -25px;
            margin-bottom: 5px;
            md-menu {
                padding: 0;
            }
        }

        .module-disabled {
            margin: -2px 0 0 12px;
            font-size: 11px;
            font-weight: 700;
            color: white;
            background: #b5b7b9;
            height: 12px;
            padding: 2px 4px;
            border-radius: 1px;
        }
    }

    &:hover {
        .mt-page-module-header {
            .mt-page-module-action > * {
                opacity: .8;
                .translate3d(0, 0, 0);
            }

        }
        .mt-add-module-button {
            .translate3d(0px, 0px, 0);
            opacity: 1;
        }
    }

}

#mt-module-picker-window {
    margin: auto;
    max-width: 960px;
    max-height: 540px;
    .spread(32px);

    .mt-modal-header {
        display: inline-block;
        .absolute();
        .top-left(0);
        .fwidth();
        background-color: #dfe4eb;
        padding: 16px;

        h3 {
            margin: 0 0 16px;
        }

        .mt-module-filter {
            margin: 0;
            min-width: 0;

            &.gradient {
                background-color: transparent;

                &::before {
                    opacity: 1;
                    transform: scale(1);
                    z-index: 0;
                }

                span {
                    color: white;
                }
            }

            &.mt-module-filter-all {
                margin-left: 0;
            }
        }
    }

    .mt-modal-content {
        margin-top: 108px;
        padding: 16px;
        max-height: calc(~"100% - 108px");
        overflow-y: scroll;

        .mt-module-group {

            h3 {
                margin: 0 0 8px;
            }

            .mt-module-wrapper {

                .mt-module-item {
                    min-width: 108px;
                    max-width: 108px;
                    margin-right: 16px;
                    .cursor();

                    p {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}

#mt-file-editor-window, #dialogContent_mt-file-editor-window {
    margin: auto;
    max-width: 960px;
    max-height: 540px;
    .spread(32px);
    overflow: hidden;

    .mt-file-placeholder {
        width: 96px;
        height: 96px;
        opacity: .35;
    }

    .mt-file-editor-display {
        .relative();
        overflow: hidden;
        background-color: rgba(10, 20, 30, .2);

        .cover-bg {
            .cover();
            .spread(-5%);
            .blur(20px);
            max-width: 110%;
            opacity: .25;
        }

        img {
            height: auto;
            max-width: 100%;
            max-height: 100%;
            width: auto;
            position: absolute;
            .spread(0);
            margin: auto;
        }
    }

    .mt-file-editor-sidebar {
        overflow-y: scroll;

        .file-editor-close {
            .absolute();
            .top-right(0);
            margin: 0;
            padding: 0;
            min-height: 32px;
            width: 32px;
            height: 32px;
            .corner();
            md-icon {
                svg {
                    fill: #c5c7c9;
                }
                &:hover svg {
                    fill: @gpink;
                }
            }
        }

        &::-webkit-scrollbar {
            display: none;
        }

        .mt-file-editor-specs {
            padding: 0;
        }

        p {
            margin: 0;

            &.mt-file-spec {
                font-size: 14px;
                padding: 2px 8px;
                font-weight: 500;

                span {
                    font-weight: 400;
                }
            }
        }

        .mt-file-editor-inputs {
            padding-top: 16px;

            md-input-container {
                // margin: -8px 8px 0;

                .md-errors-spacer {
                    display: none;
                }
            }
        }
    }
}

#mt-file-upload-window, #dialogContent_mt-file-upload-window {

    .mt-file-upload-img-wrapper {

        input {
            min-height: 200px;
            max-height: 200px;
            opacity: 0;
        }

        .mt-file-upload-img-preview {
            pointer-events: none;
            .absolute();
            .top-right(0);
            width: 100%;
            min-height: 200px;
            max-height: 200px;
            overflow: hidden;
            background-color: rgba(10, 20, 30, .4);

            img {
                max-height: 200px;
                max-width: 100%;
                height: auto;
                width: auto;
            }
        }
    }

    .input-wrapper {

        md-input-container {
            margin: 0;

            .md-errors-spacer {
                display: none;
            }
        }
    }

    .file-upload-loader {
        background-color: rgba(255, 255, 255, .85);
        .absolute();
        .spread(0);

        md-icon {
            .absolute();
            .spread(0);
            width: 64px;
            height: 64px;
            margin: auto;
            animation: upload 1.5s ease-out infinite;

            svg {
                fill: #2196F3;
            }
        }
    }
}

.mt-notification-sidebar {

    font-size: 90%;
    box-shadow: 0 0 30px rgba(0, 0, 0, .2);

    md-toolbar:not(.md-menu-toolbar) {
        background-color: @base_grey;
        //.gradient-soft();
    }

    .mt-notifications-wrapper {

        .mt-notification-item {
            padding: 10px 25px;
            border-bottom: 1px solid #e5e7e9;
            .relative();
            overflow: hidden;
            .remove {
                .cursor();
                .absolute();
                .top-right(10px, 10px);
                font-size: 150%;
                .transition(all .2s ease);
                .translate3d(5px, 0px, 0);
                opacity: 0;
                color: #a5a7a9;
                &:hover {
                    color: @gpink;
                }

            }

            h4 {
                margin: 0;
                padding: 5px 0;
            }

            small {
                margin: 0;
                padding: 5px 0;
                display: block;
                color: #858789;
            }

            &.mt-notif-priority {

                &-1 {
                    h5 {
                        background-color: @ggreen;
                        color: white;
                    }
                }
                &-2 {
                    h5 {
                        background-color: @gpurple;
                        color: white;
                    }
                }
                &-3 {
                    h5 {
                        background-color: @gblue;
                        color: white;
                    }
                }
                &-4 {
                    h5 {
                        background-color: @gyellow;
                    }
                }
                &-5 {
                    h5 {
                        background-color: @gred;
                        color: white;
                    }
                }
            }
            &:hover {
                .remove {
                    .translate3d(0px, 0px, 0);
                    opacity: 1;
                }
            }
        }
    }
}

mt-input-container {
    display: block;

    .mt-module-title {
        color: #b5b7b9;
        font-size: 11px;
        text-transform: uppercase;
        font-weight: 700;
        cursor: default;
    }

    medium-editor, [medium-editor] {

        &::selection {
            background: black;
            color: #ccc;
        }
    }
}

mt-warning {
    font-size: 12px;
    color: #ff6080;
    line-height: 36px;
}

mt-no-padding,
[mt-no-padding],
.mt-no-padding {
    padding: 0;
}

mt-module-duplicator, [mt-module-duplicator], .mt-module-duplicator {
    max-height: 196px !important;
}

.mt-loading-wrapper {
    .absolute();
    .spread(0);
    z-index: 50;
    background-color: rgba(255, 255, 255, .35);

    .mt-loading-spinner {
        .absolute();
        .spread(0);
        display: block;
        height: 96px;
        width: 96px;
        margin: auto;

        md-icon {
            height: 96px;
            width: 96px;
            animation: upload 1.5s ease-out infinite;

            svg {
                fill: #2196F3;
            }
        }
    }
}

#mt-add-user-dialog {
    width: 480px;

    md-input-container {
        margin: 0;
    }
}
