.mt-page-module-wrapper {
    border-radius: 2px;
    [data-medium-editor-element] {
        font-size: 90%;
        line-height: 1.6;

    }
    md-input-container {
        margin: 20px 10px 0px;

        .md-input {
            line-height: 1.6;
            font-size: 90%;
            &:not(.bordered) {
                border: none;
            }
            &.smaller {
                font-size: 90%;
            }
            &.larger {
                font-size: 120%;
                font-weight: 300;
            }
        }
    }

    md-input-container.md-input-focused label:not(.md-no-float), md-input-container.md-input-has-placeholder label:not(.md-no-float), md-input-container.md-input-has-value label:not(.md-no-float) {
        -webkit-transform: translate3d(0,12px,0) scale(.75);
        transform: translate3d(0,12px,0) scale(.75);
    }
    mt-input-container {
        padding: 0 20px;
        .mt-module-title {
            margin: 6px 0px;
        }
    }
    .max-1000 {
        max-width: 1000px;
        margin: 20px auto;
        .relative();
    }

    .non-editable {
        background-color: #f5f7f9;
        padding: 20px;
        margin: -20px 0;
        p {
            margin: 10px 0 0px;
        }
        pre {
            margin: 5px 0 20px;
            color:#858789;
        }
    }

    .mt-module-block {

        &.mt-bg-image {
            .relative();
            height: 144px;
            display: flex;
            justify-content: center;
            .cover();
            .cursor();
            .transition(opacity .2s ease);
            .translate3d(0, 0, 0);

            .mt-module-remove-button, .change-bg {
                .absolute();
                .top-right(0);
                border: 0;
                background-color: @gpink;
                padding: 0;
                z-index: 1;
                .transition(all .2s ease);
                opacity: 0;
                .scale(.6);
            }
            .change-bg {
                i {
                    font-size: 140%;
                    position: relative;
                    top: 3px
                }
            }

            &.auto-height {
                height: auto;
            }
            &.wide {
                width: 100%;
                max-width: 100%;
                height: 240px;
            }
            &.square {
                height: 240px;
            }
            &.has-text {
                label, input, textarea {
                    color: #fff;
                }
                cursor: default;
            }
            .overlay {
                .spread();
                background-color: rgba(10, 20, 30, .6);
            }
        }

        &.mt-add-image {
            .cursor();
            border: 1px dashed #d5d7d9;
            md-icon {
                width: 32px;
                height: 32px;
            }
            &.square {
                height: 240px;
            }
        }

        &:hover {
            opacity: .9;
            .mt-module-remove-button, .change-bg {
                opacity: 1;
                .scale(1);
            }
        }

    }
    .module-fullscreen {
        min-height: 300px;

        .bg-wrapper {
            .cover();
            .spread();
            color: #fff;

            .bg-header-overlay {
                .absolute();
                height: 70px;
                width: 100%;
                top: 0;
                left: 0;
                background-color: rgba(255, 255, 255, .95);
                overflow: hidden;

            }
            .overlay {
                .spread();
                background-color: rgba(10, 20, 40, .4);
            }
            .content-wrapper {
                .spread();
                margin: auto;
                width: 100%;
                max-width: 500px;
                text-align: center;
                height: 140px;
                color: #fff;

                md-input-container {
                    width: 100%;
                    margin-top: 50px;
                    input, label {
                        color: #fff !important;
                    }
                    input {
                        border-bottom: 1px solid rgba(255, 255, 255, .5);
                        width: 100%;
                    }
                }

                .btn {
                    max-width: 300px;
                    margin: auto;

                }
            }
        }

    }
}

.mt-module--block {

    &.mt-module--sliders-fullscreen {

        .mt-module--item {

            .mt-module__image-wrapper {
                height: 196px;
            }
        }

        mt-module-duplicator {
            height: 196px;
            margin-top: 32px;
        }
    }
}

.mt-module__image-wrapper {
    height: 196px;
    overflow: hidden;
    background-color: rgba(100, 110, 130, .4);
}

mt-module-block {
    display: block;
}

.mt-bg-image {
    background-size: cover;
    background-position: center;
}
