#mt-page-dashboard {

    md-toolbar {
        //color: #fff !important;
        .transition(background-color .15s ease);
        span.flex {
            min-height: 0 !important;
        }
        .toolbar-tools {
            padding-bottom:0 !important;
        }

        form {
            ::-webkit-input-placeholder {
                color: rgba(10, 20, 30, .8);
            }

            :-moz-placeholder {
                color: rgba(10, 20, 30, .8);
            }

            ::-moz-placeholder {
                color: rgba(10, 20, 30, .8);
            }

            :-ms-input-placeholder {
                color: rgba(10, 20, 30, .8);
            }
            md-datepicker {
                background: transparent;
                .md-datepicker-triangle-button {
                    top:5px;
                }
            }
            .md-datepicker-input-container {
                margin-left: 0;
                border-bottom-color: rgba(10,20,30,.3);
                .md-datepicker-focused {
                    border-bottom: 1px solid rgba(10, 20, 30, 1);
                }
            }
            //.md-datepicker-input-container.md-datepicker-focused {
            //    border-bottom: 1px solid rgba(255, 255, 255, 1);
            //}
            //input {
            //    color: #fff;
            //    width: 100%;
            //}
            //md-icon.md-default-theme, md-icon {
            //    color: rgba(255, 255, 255, .7);
            //}
            //.md-datepicker-open .md-datepicker-calendar-icon {
            //    fill: rgba(255, 255, 255, .2);
            //}
            //.md-button.md-icon-button {
            //    // Dude don't do this, circular shadow is squashed horizontally
            //    // width: 30px;
            //    padding: 0;
            //    margin: 0;
            //}

            //md-datepicker.md-default-theme, md-datepicker {
            //    background-color: transparent;
            //    width: 100%;
            //
            //}
            //.md-default-theme .md-datepicker-triangle-button .md-datepicker-expand-triangle, .md-datepicker-triangle-button .md-datepicker-expand-triangle {
            //    border-top-color: rgba(255, 255, 255, .7);
            //}

        }
    }

    .analytics-wrapper {
        // max-height: calc(~"100vh - 64px");
        // overflow-y: scroll;

        .main-stat {

            width: 100%;
            padding: 50px 20px 80px 30px;
            background:#e5e7e9;

            .stat-graph {
                padding-right: 30px;
            }
            .stat-heading {
                padding-left: 20px;
                h5 {
                    text-transform: uppercase;
                    font-size: 80%;
                    margin: 10px 0 20px;
                }
            }
            .stat-text {
                form {
                    margin-top: 50px;
                    ::-webkit-input-placeholder {
                        color: rgba(255, 255, 255, .8);
                    }

                    :-moz-placeholder {
                        color: rgba(255, 255, 255, .8);
                    }

                    ::-moz-placeholder {
                        color: rgba(255, 255, 255, .8);
                    }

                    :-ms-input-placeholder {
                        color: rgba(255, 255, 255, .8);
                    }
                    .md-datepicker-input-container {
                        border-bottom-color: rgba(255, 255, 255, .3);
                        margin-left: 5px;
                        width: 70%;
                    }
                    .md-datepicker-input-container.md-datepicker-focused {
                        border-bottom: 1px solid rgba(255, 255, 255, 1);
                    }
                    input {
                        color: #fff;
                        width: 100%;
                    }
                    md-icon.md-default-theme, md-icon {
                        color: rgba(255, 255, 255, .7);
                    }
                    .md-datepicker-open .md-datepicker-calendar-icon {
                        fill: rgba(255, 255, 255, .2);
                    }
                    .md-button.md-icon-button {
                        // Dude don't do this, circular shadow is squashed horizontally
                        // width: 30px;
                        padding: 0;
                        margin: 0;
                    }

                    md-datepicker.md-default-theme, md-datepicker {
                        background-color: transparent;
                        width: 100%;

                    }
                    .md-default-theme .md-datepicker-triangle-button .md-datepicker-expand-triangle, .md-datepicker-triangle-button .md-datepicker-expand-triangle {
                        border-top-color: rgba(255, 255, 255, .7);
                    }

                }
                .info-box {
                    padding-left: 20px;
                    h5 {
                        text-transform: uppercase;
                        font-size: 80%;
                        margin: 10px 0 0;
                    }
                    h1 {
                        font-weight: 100;
                        margin: 10px 0;
                        font-size: 280%;
                        border-top:1px solid rgba(255,255,255,.1);
                        padding-top:5px;
                        margin-right:10px;
                        small {
                            font-size: 40%;
                            font-weight: 400;
                            color: #24ffbf;
                            &.drop {
                                color: #ffda5b;
                            }
                        }
                    }
                    table {
                        width: 100%;
                        tr {
                            td {
                                border-bottom: 1px solid rgba(10,20,30,.1);
                                padding: 10px 10px;
                            }
                        }
                    }
                }
            }
            &.blue,&.purple,&.pink {
                .stat-text {
                    .info-box {

                        h1 {color:#fff}
                        h5 {color: rgba(255, 255, 255, .5);}
                    }
                }
                strong {
                    font-weight: 400;
                    color:#fff;
                }
            }
            &.blue {
                .gradient-blue();
                color: #fff;
                h5 {color: rgba(255, 255, 255, .5);}
                table {
                    tr {
                        td {
                            color: rgba(255,255,255,.7);
                            border-bottom-color: rgba(255,255,255,.3) !important;
                        }
                    }
                }
            }
            &.purple {
                .gradient-purple();
                color: rgba(255,255,255,.5);
                table {
                    tr {
                        td {
                            border-bottom-color: rgba(255,255,255,.1) !important;
                        }
                    }
                }


            }
            &.pink {
                .gradient-hard();
                color: #fff;
            }
        }
    }
}

#mt-page-content-nav-edit {

    .mt-data-container {

        .mt-navigation-item {
            .cursor();

            span {
                display: inline-block;
                padding: 8px 0;
                font-size: 95%
            }

            h3 {
                .cursor();
                margin: 4px 0;
                padding: 12px;
            }

            md-menu {
                padding: 0;
            }

            &.nav-item-parent {
                margin-left: 16px;
                margin-bottom: 5px;
                background-color: #fff;
                padding: 15px;
            }

            &.nav-item-child {
                margin-left: 25px;
            }

            &.nav-item-grand {
                margin-left: 50px;
            }
        }
    }
}

.mt-page-card-content {

    md-toolbar {

    }

    .mt-data-container {

        .mt-card-wrapper {

            .mt-card-item {
                background-color: white;
                padding: 8px;
                border-radius: 2px;
                box-shadow: 0 20px 50px -15px rgba(0, 0, 0, 0.25);

                .mt-card-thumbnail-wrapper {
                    width: 196px;
                    height: 144px;
                    background-color: rgba(40, 30, 20, .1);
                }

                p {
                    margin-bottom: 0;
                }
            }
        }
    }
}

.mt-config-group {

    .mt-config-item {
        padding: 10px 0;
        border-bottom: 1px solid #e5e7e9;
        .transition(background-color .1s linear);

        &:last-child {
            border: none;
        }

        h5 {
            margin: 0;
        }

        p {
            margin: 0;
        }
        small {
            color: #a5a7a9;
            display: block;
            margin-top: 5px;
        }

        .avatar {
            width: 60px;
            height: 60px;
            margin-right: 10px;
            img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
            }
        }

        md-switch {
            margin: 0;
        }

        md-input-container {

            .md-errors-spacer {
                display: none;
            }
        }
    }
}

#mt-page-content-file-manager,
#mt-page-content-file-picker {
    //background-color: #f2f2f2;

    &.mt-file-picker-dialog {
        .spread(32px);
        margin: auto;
        max-width: 960px;
        max-height: 540px;
    }

    .md-toolbar-header {

        .toolbar-tools {

            .upload-button {
                md-icon {

                }
            }
        }

        .mt-file-breadcrumbs {
            padding-left: 16px;
            margin-bottom: 16px;

            .mt-file-breadcrumb {

                button {
                    padding: 0px 12px;
                    text-transform: none;
                    margin: 0;
                    min-width: 0;
                    color: #646464;
                    border: 0px solid transparent;

                    &:hover {
                        background-color: #fff;
                        box-shadow: 0 6px 20px -5px rgba(0, 0, 0, 0.1);
                    }
                }

                .folder-history-button {
                    border-radius: 2px;
                    position: relative;
                }

                .chevron-separator {
                    margin-right: 4px;
                }

                .mt-menu-group-item {

                    button {

                        md-icon {
                            margin-left: 8px;
                        }
                    }
                }
            }
        }
    }

    .mt-data-container {

        .mt-file-manager-item {

            .mt-file-item-card {
                padding: 0;
                background-color: white;
                box-shadow: 0 20px 50px -15px rgba(0, 0, 0, 0.25);
                .translate3d(0, 0, 0);
                .relative();
                overflow: hidden;
                md-icon {
                    padding: 10px 12px;
                    fill: #fff;
                }

                .menu-icon {
                    padding: 9px 12px;
                    background-color: rgba(10, 20, 30, .03);
                    color: #c5c7c9;
                    font-size: 130%;
                }

                p {
                    margin: 0;
                    padding: 12px;
                    font-size: 14px;
                }

                .mt-file-actions {

                    .absolute();
                    bottom: 0;
                    left: 0;
                    right: 0;
                    cursor: pointer;
                    color: #fff;
                    .gradient-black();
                    padding-top: 40px;
                    opacity: 0;
                    .translate3d(0, 5px, 0);
                    .transition(all .2s ease);

                    p {
                        padding: 12px;
                    }

                    md-menu {
                        padding: 0;

                        md-icon {
                            padding: 0;
                        }
                    }
                }

                md-menu {
                    //opacity: 0;
                }

                &.folder-card {

                    md-menu {
                        opacity: 0;
                        padding: 0;

                        button {
                            padding: 0;

                            md-icon {
                                padding: 0;
                            }
                        }
                    }
                }

                &:hover {

                    .mt-file-actions {
                        opacity: 1;
                        .translate3d(0, 0px, 0);
                    }
                }
            }

            .mt-file-item-img-wrapper {
                .relative();
                min-height: 128px;
                max-height: 128px;
                overflow: hidden;
                background-color: rgba(100, 110, 130, .4);
                .cover();
                &.small {
                    background-size: auto;
                }

                img {
                    height: auto;
                    max-width: 100%;
                }
            }
        }
    }
}

#mt-page-content-page-edit {

    .mt-data-container {

        form {

            .mt-add-module-button {
                text-align: center;
                .cursor();
                .translate3d(0px, -5px, 0);
                .transition(opacity .2s ease);
                // opacity: 0;
                font-size: 11px;
                text-transform: uppercase;
                font-weight: 700;
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}

