.data-tooltip {
    border-radius: 10px;
    padding: 6px;
    color: #666;
    background: rgba(255, 255, 255, 0.8);
    border: solid 2px rgba(230, 230, 230, 0.8);
    font-family: sans-serif;
    font-size: 12px;
    text-align: center;
    position: fixed;
    white-space: nowrap;
}
.data-tooltip .data-tooltip-header {
    font-weight: bold;
    margin: 0.25em 0;
}
.data-tooltip .data-tooltip-value {
    color: #0b62a4;
    margin: 0.1em 0;
}
