html {

    * {
        outline: 0;
    }

    body {
        height: 100vh;
        width: 100vw;
        overflow: hidden;
        font-family: 'Proxima Nova', sans-serif;
        //.gradient-linear(#614385, #516395);

        button, select, html, textarea, input {font-family: 'Proxima Nova', sans-serif;}

        mt-app-wrapper {
            .spread();
            margin: auto;
            .box-shadow();
            overflow: hidden;
            background-color: #dfe4eb;

            #mt-sidenav {

            }

            .mt-page-template {
                .transition(all .3s ease);
            }

            #md-content-right {
                .absolute();
                z-index: 50;
                left: 64px;
                top: 0;
                background-color: #f1f3f7;
                width: calc(~"100% - 64px");
                height: 100%;
                transition: all .22s ease;

                #app-view {
                    .absolute();
                    .spread();

                    > md-content {
                        height: 100%;
                        width: 100%;

                        > md-toolbar {
                            background-color: rgba(241,243,247,.95);
                            color: @base_dark;

                            .md-button.btn {
                                margin:0 5px 0 0;
                            }
                            &.md-toolbar-header {
                                //min-height: 128px;
                                max-height: 128px;
                                span.flex {
                                    min-height: 20px
                                }
                                md-list {
                                    padding: 0;

                                    .mt-table-header {
                                        .noselect();
                                        font-size: 12px;
                                        text-transform: uppercase;
                                        font-weight: 600;
                                        color: #646464;
                                        letter-spacing: .75px;

                                        mt-button-wrapper {
                                            .absolute();
                                            right: 16px;
                                        }

                                        p {
                                            margin: 0;
                                        }
                                    }
                                }
                            }

                            &.md-toolbar-search {
                                min-height: 196px;
                                max-height: 196px;
                            }

                            .toolbar-tools {
                                padding: 0 20px 12px 30px;
                                .edit-notif {
                                    color: @gpink;
                                    font-size:25px;
                                    cursor: pointer;
                                }

                                h2 {
                                    margin: 0;
                                    font-size: 24px;
                                    font-weight: 200;
                                    line-height: 30px;
                                }

                                .batch-menu {

                                    md-menu {
                                        margin: 10px auto;
                                    }

                                    > .toolbar-button {
                                        .transition(all .11s ease);

                                        &:nth-last-child(2) {
                                            .transition-delay(.3s);
                                        }

                                        &:nth-last-child(3) {
                                            .transition-delay(.6s);
                                        }
                                    }
                                }
                            }
                        }

                        .mt-data-container {
                            .relative();
                            overflow-y: scroll;
                            height: calc(~"100vh - 128px");

                            md-list {

                                md-list-item {

                                    p {
                                        .ellipsis();
                                        padding-right: 24px;
                                    }

                                    md-menu {
                                        position: absolute;
                                        top: 0;
                                        right: 16px;
                                        bottom: 0;
                                        margin: auto;
                                    }

                                    &:not(.mt-table-header):hover {
                                        background-color: #fafafa;
                                    }
                                }
                            }
                        }
                    }

                    .mt-auth-wrapper {
                        .spread();
                        margin: auto;
                        width: 240px;
                        height: 220px;
                        color:#fff;
                        ::-webkit-input-placeholder {
                            color: rgba(255,255,255,.6);
                        }
                        :-moz-placeholder {
                            color: rgba(255,255,255,.6);
                        }
                        ::-moz-placeholder {
                            color: rgba(255,255,255,.6);
                        }
                        :-ms-input-placeholder {
                            color: rgba(255,255,255,.6);
                        }
                        form {
                            height: 100%;

                            h3 {
                                margin: 0 0 32px 0;
                            }

                            md-input-container {
                                margin: 0;

                                input {
                                    color:#fff;
                                    border-bottom: 1px solid rgba(255,255,255,.3);
                                    -webkit-text-fill-color: rgba(255,255,255,.8) ;
                                    &:-webkit-autofill,
                                    &:-webkit-autofill:hover,
                                    &:-webkit-autofill:focus,
                                    &:-webkit-autofill:active {
                                        transition: background-color 5000s ease-in-out 0s;
                                    }

                                }

                            }
                        }
                    }
                }
            }
        }

        &.aside {

            .nav-group-tooltip {
                display: none;
            }

            #mt-sidenav {

                #mt-menu-group-wrapper {

                    mt-menu-item {

                    }
                }
            }

            #md-content-right {
                .box-shadow();
                transform: translate3d(180px, 0, 0) scale(.9);

                > * {
                    pointer-events: none;
                }
            }
        }

        &.sidelocked {

            #md-content-right {
                width: calc(~"100% - 304px");
                .translate3d(240px, 0, 0);
                box-shadow: none;
            }
        }

        &.authScreen {

            mt-app-wrapper {

                #mt-sidenav {
                    display: none;
                }

                #md-content-right {
                    width: 100%;
                    left: 0;
                    .translate3d(0,0,0);

                    .auth-bg {
                        .spread(0);
                        .gradient-blue();
                    }
                    .auth-overlay {
                        .spread(0);

                        .cover();
                        opacity: .2;
                    }
                }
            }
        }

        #dialogContent_mt-file-manager-window {
            .absolute();
            .spread(32px);
            background-color: white;
            transition: all .33s ease;
            max-height: 100%;
            max-width: 100%;
        }

        #dialogContent_mt-file-manager-window {

            .mt-file-manager-content {

                .mt-file-wrapper {
                    height: 196px;

                    .mt-file-image-wrapper {
                        .relative();
                        height: 144px;
                        text-align: center;

                        img {
                            max-height: 100%;
                            max-width: 100%;
                            height: auto;
                            width: auto;
                        }
                    }

                    .mt-file-name {
                        margin: 0;
                        padding: 4px;
                    }
                }
            }
        }
        &.md-dialog-is-showing .mt-page-template{
            //-webkit-filter: blur(5px);
            //-moz-filter: blur(5px);
            //-o-filter: blur(5px);
            //-ms-filter: blur(5px);
            //filter: blur(5px);
        }
    }
}


