hr {
    background: #e5e7e9;
    height: 1px;
    width: 100%;
    border: none;
    margin: 30px 0;
}


.menu-icon {
    margin: auto;
    vertical-align: middle;
    height: 24px;
    width: 24px;
    font-size: 100%;
