@ncolor: #ff598f;

#nprogress {
    .absolute();
    z-index: 100;

    .bar {
        .gradient-hard();
        height: 4px;
    }

    .peg {
        // box-shadow: 0 0 10px @ncolor, 0 0 5px @ncolor;
        box-shadow: none;
    }

    .spinner-icon {
        border-top-color: @ncolor;
        border-left-color: @ncolor;
    }
}

md-checkbox {

    .md-icon {
        border-width: 1px;
        .rounded();
        border-color: #c5c7c9;

        &:after {
            transform: rotate(45deg) scale(.8);
        }
    }

    &.md-checked {

        .md-icon {
            .gradient-soft();

            &:after {
                transform: rotate(45deg) scale(.8);
            }
        }
    }
}

.mt-button {
    border: 2px solid rgba(255, 255, 255, 0.65);
    border-radius: 14px;
    height: 28px;
    line-height: 26px;
    box-sizing: border-box;
    font-size: 12px;
    color: white;
    background: transparent;
    font-weight: 700;
    padding: 0 12px;
}

.md-button {

    &.btn {
        box-shadow: none !important;
        border-radius: 0 !important;
        font-size: 12px;
        text-transform: uppercase;
        font-weight: 700;
        background: transparent;
        padding: 0 20px;
        position: relative;
        overflow: hidden;
        .transition(all .3s ease);

        .md-ripple-container {
            display: none;
        }

        * {
            position: relative;
            .transition(color .3s ease);
            color: @base_grey;
        }

        &:hover {
            background-color: #fff;
            box-shadow: 0 0 0 1px rgba(0, 0, 0, .05), 0 6px 20px -5px rgba(0, 0, 0, 0.1) !important;

            * {
                color: @base_dark;
            }
        }

        i.menu-icon {
            font-size: 160%;
            line-height: 1;
            margin-right: 10px;
            top: 5px;
        }

        &.gradient {
            background: rgba(10, 20, 30, .05);
            //.transition(background-color .4s ease);

            &:before {
                content: "";
                .spread();
                .gradient-soft();
                .transition(all .3s ease);
                opacity: 0;
                transform: scale(.9);
            }

            &:hover {
                color: #fff;
                //background: transparent;
                box-shadow: none !important;

                &:before {
                    opacity: 1;
                    transform: scale(1.01);
                }

                * {
                    color: #fff;
                }
            }
        }
        &.black {
            background-color: #000;
            color: #fff;
            * {
                color:#fff
            }
        }
        &.white {
            background-color: #fff;
            color: #000;
            * {
                color:#000
            }
        }

        &:disabled {
            color: #a5a7a9;

            * {
                color: #a5a7a9;
            }

            &:before {
                display: none;
            }

            &:hover {
                background: rgba(10, 20, 30, .05);
                color: #a5a7a9;
                box-shadow: none !important;

                * {
                    color: #a5a7a9;
                }
            }
        }
    }
}

md-input-container label:not(.md-no-float):not(.md-container-ignore), md-input-container .md-placeholder {
    color: #b5b7b9 !important;
    padding-left: 10px;
}
.mt-page-module-wrapper md-input-container.md-input-focused label:not(.md-no-float), .mt-page-module-wrapper md-input-container.md-input-has-placeholder label:not(.md-no-float), .mt-page-module-wrapper md-input-container.md-input-has-value label:not(.md-no-float) {
    padding-left:16px;
}
md-input-container.md-input-focused label:not(.md-no-float),
md-input-container.md-input-has-placeholder label:not(.md-no-float),
md-input-container.md-input-has-value label:not(.md-no-float) {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 90%;
    margin-bottom: 5px
}

md-switch {

    .md-container {

        .md-bar {
            left: -2px;
            width: 40px;
            top: 0;
            height: 24px;
            border-radius: 12px;
            background-color: rgba(10, 20, 30, .15);
        }

        .md-thumb-container {
            top: 2px;

            .md-thumb {
                background-color: rgba(255, 255, 255, .9)
            }
        }
    }

    &.md-checked {

        .md-bar {
            left: -2px;
            width: 40px;
            top: 0;
            height: 24px;
            border-radius: 12px;
            .gradient-soft();
        }

    }
}

md-icon {

    &.md-icon-dark, &[md-icon-dark] {

        svg {
            fill: @base_dark;
        }
    }

    &.md-icon-grey, &[md-icon-grey] {

        svg {
            fill: #646464;
        }
    }

    &.md-icon-ghost, &[md-icon-ghost] {

        svg {
            fill: #d3d3d3;
        }
    }
}

md-menu-content {

    md-menu-item {

        a {
            line-height: 48px !important;
        }
    }
}

.md-input {
    //.transition(border .2s linear);
}

md-input-container.md-default-theme:not(.md-input-invalid).md-input-focused .md-input,
md-input-container:not(.md-input-invalid).md-input-focused .md-input,
md-select.md-default-theme:not([disabled]):focus .md-select-value,
md-select:not([disabled]):focus .md-select-value {
    border-color: #ff598f;
}

md-list {

    > h3 {
        padding: 27px 20% 10px;
        font-size: 13px;
        color: #646464;
        text-transform: uppercase;
    }
}

md-toolbar {

    .md-tall {
        background-color: #fafafa;
        color: @base_dark;
        border-bottom: 1px solid #f1f1f1;

        .md-toolbar-tools {
            //padding: 12px 16px !important;

            h2 {
            }
        }
    }
}

md-content.md-default-theme, md-content {
    background-color: transparent;
}

.md-whiteframe-1dp, .md-whiteframe-z1 {
    box-shadow: none
}

.md-toolbar-search-wrapper {
    .box-shadow();

    md-input-container {
        margin: 0;
        padding: 0;

        md-select {
            width: 140px;
            background: @base_dark;
            color: white !important;
            padding: 5px 8px;
            font-size: 12px;
            text-transform: uppercase;
            font-weight: 600;

            md-select-value {
                .translate3d(0, -2px, 0);
                letter-spacing: 1px;
                border: 0 !important;

            }
        }
    }

    input {
        font-size: 14px;
        padding: 11px;
        border: 0;
    }
}

md-select-menu.md-default-theme md-option[selected] .md-text, md-select-menu md-option[selected] .md-text {
    color: rgb(255, 108, 119) !important;
    font-weight: 700;
}

.md-toolbar-search-wrapper md-input-container md-select:not([disabled]):focus .md-select-value {
    color: white;
    padding-bottom: 1px;
}

md-dialog {
    border-radius: 2px;

    &.md-default-theme {
        border-radius: 2px;
    }
}

md-backdrop.md-opaque.md-default-theme, md-backdrop.md-opaque {
    background-color: rgba(10, 20, 30, 1.0);
}

md-select-menu {
    background-color: #fff;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1), 0 20px 45px -12px rgba(0, 0, 0, 0.15), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
}

md-menu-item {
    button, a {
        min-width: 100% !important;
    }
    md-switch {
        position: relative;
        top: -10px;
        .md-container {
            left:-5px;
            .cursor();
        }
    }
}

.md-whiteframe-4dp, .md-whiteframe-z2 {
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1), 0 20px 45px -12px rgba(0, 0, 0, 0.15), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
}

md-input-container {
    padding: 2px 10px;

    .md-errors-spacer {
        display: none;
    }
}

@media (min-width: 960px) {
    .md-padding {
        padding: 25px;
    }
}



.md-calendar-day-header {
    background-color: rgba(255,255,255,.4)
}
.md-calendar-scroll-container {
    box-shadow: inset 0 6px 20px -10px rgba(0, 0, 0, 0.15);
}
.md-datepicker-calendar-pane {
    border:none;
    box-shadow: 0 20px 50px -15px rgba(0, 0, 0, 0.45);
}
.md-datepicker-calendar {
    background: rgba(255,255,255,.95);
}

.md-calendar-date-selection-indicator {
    line-height: 44px;
}

.md-calendar-date-selection-indicator:hover {
    background: rgba(10,20,30,.05);
}
.md-calendar-date.md-focus .md-calendar-date-selection-indicator {
    background-color: transparent;
    border-color: transparent;
    .gradient-soft();
    color:#fff;
}

.md-select-value .md-select-icon:after {
    color:#a5a7a9;
}

